* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
}

.header__content {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-height: 60px;
  padding: 0 50px;
  display: flex;
}

img {
  height: 40px;
  display: block;
}

.nav__list {
  column-gap: 20px;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  display: inline-block;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  opacity: 1;
  background-color: #00acdc;
  border-radius: 9px;
  width: 100%;
  height: 5px;
  position: absolute;
  bottom: -1px;
  left: 0;
}

